.toolbar__item.icon-button{padding:0 .4rem;cursor:unset;list-style:none}.toolbar__item.icon-button button.icon-button__icon{position:relative;display:inline-block;color:var(--navbar-text-color);font-size:1.8rem;border-radius:50%;border:1px solid var(--navbar-text-color);width:3.6rem;height:3.6rem}@media screen and (min-width: 415px){.toolbar__item.icon-button button.icon-button__icon{width:4.2rem;height:4.2rem;font-size:2.2rem}}.toolbar__item.icon-button button.icon-button__icon:hover{background-color:var(--navbar-active-item-bg-color)}.toolbar__item.icon-button button.icon-button__icon.has-badge::after{content:"";display:block;position:absolute;right:0;top:0;width:1rem;height:1rem;border-radius:1rem;z-index:1;background-color:var(--primary-1);animation:badge-pulse 3s ease-out infinite}@keyframes badge-pulse{0%{transform:scale(1)}8%{transform:scale(1.2)}15%{transform:scale(0.7)}21%{transform:scale(1)}100%{transform:scale(1)}}