.navigation-bar{position:fixed;top:0;left:0;width:100%;height:max-content;z-index:10}.navigation-bar .toolbar-container{position:relative;width:100%;height:6.8rem;display:flex;flex-direction:row;align-items:center;justify-content:space-between;background-color:var(--navbar-bg-color);padding:0 1.2rem;border-bottom:1px solid var(--navbar-divider-color)}@media screen and (min-width: 769px){.navigation-bar .toolbar-container{padding:0 2.8rem}}.navigation-bar .toolbar-container .toolbar__nav-link-container,.navigation-bar .toolbar-container .toolbar__icon-btn-container{display:flex;flex-direction:row;align-items:stretch;justify-content:flex-start;height:100%;width:max-content;flex-shrink:0}.navigation-bar .toolbar-container .toolbar__nav-link-container{margin-right:3.2rem}.navigation-bar .toolbar-container .toolbar__item{display:inline-flex;align-items:center;cursor:pointer;flex-shrink:0;width:max-content;height:auto}.navigation-bar .toolbar-container .toolbar__item.logo{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);margin-right:0}.navigation-bar .toolbar-container .toolbar__item.logo a{position:relative;display:inline-block;cursor:pointer}.navigation-bar .toolbar-container .toolbar__item.logo .toolbar__logo-image{position:relative;display:block;height:calc(6.8rem * 0.65);width:auto;border:1px solid var(--navbar-text-color)}@media screen and (min-width: 769px){.navigation-bar .toolbar-container .toolbar__item.logo{position:relative;top:unset;left:unset;transform:unset;margin-right:3.2rem}.navigation-bar .toolbar-container .toolbar__item.logo .toolbar__logo-image{height:calc(6.8rem * 0.75)}}