.image-loader-box{position:relative;display:inline-block;width:100%}.image-loader-box>img{position:relative;display:none;width:100%;height:auto}.image-loader-box>.image-loader-box__loading-animation{position:relative;display:inline-block;width:100%;min-height:var(--image-min-height);background-color:var(--image-loader-bg-color);overflow:hidden}.image-loader-box>.image-loader-box__loading-animation::after{content:"";display:block;position:absolute;left:0;top:0;width:100%;height:100%;z-index:1;transform:translateX(-150%);background:linear-gradient(90deg, transparent, var(--image-loader-gradient-color), transparent);opacity:.8;animation:image-loader-ani 2.5s infinite}.image-loader-box.is-loaded img{display:inline-block}.image-loader-box.is-loaded .image-loader-box__loading-animation{display:none}@keyframes image-loader-ani{from{transform:translateX(-150%)}to{transform:translateX(150%)}}