.search-widget__search-item{position:relative;display:flex;align-items:center;justify-content:flex-start;border:1px solid var(--search-widget-border-color);padding:1rem;background-color:var(--search-widget-bg-color);cursor:pointer}.search-widget__search-item:not(:last-child){margin-bottom:1.8rem}.search-widget__search-item:hover,.search-widget__search-item:active{background-color:var(--search-widget-item-hover-bg-color)}.search-widget__search-item .search-item__img{position:relative;display:inline-block;width:8.6rem;height:8.6rem;border:1px solid var(--search-widget-border-color);margin-right:1.2rem;padding:.3rem;flex-shrink:0}.search-widget__search-item .search-item__img>img{object-fit:cover}.search-widget__search-item .search-item__img>img,.search-widget__search-item .search-item__img>.image-loader-box__loading-animation{width:100%;height:100%;min-height:unset}.search-widget__search-item .search-item__item-details{position:relative;display:inline-flex;flex-direction:column;align-items:flex-start;justify-content:center;flex-grow:1}.search-widget__search-item .search-item__item-details>*{display:block}.search-widget__search-item .search-item__item-details .item-details__name{margin-bottom:.2rem;font-size:1.4rem;font-weight:600;color:var(--search-widget-feature-color)}.search-widget__search-item .search-item__item-details .item-details__name>*{display:inline-block}.search-widget__search-item .search-item__item-details .item-details__name>.highlighted{background-color:var(--search-widget-highlight-bg-color);color:var(--search-widget-highlight-text-color)}.search-widget__search-item .search-item__item-details .item-details__price{font-size:1.8rem;font-weight:600}