.amount-selector{position:relative;display:inline-flex;align-items:center;justify-content:flex-start}.amount-selector>*{position:relative;display:inline-block}.amount-selector__input{width:3.4rem;height:3.4rem;line-height:3.4rem;font-size:1.4rem;text-align:center;border:1px solid var(--divider-color);margin-right:.6rem;letter-spacing:1px}.amount-selector__input:focus{border:1px solid var(--secondary-1)}.amount-selector__btn{width:3.4rem;height:3.4rem;border:1px solid var(--divider-color);cursor:pointer;font-size:var(--size-1);font-weight:600;user-select:none;color:var(--amount-selector-text-color)}.amount-selector__btn:hover{background-color:var(--amount-selector-text-color);color:var(--amount-selector-text-color_hover)}.amount-selector__btn.plus{margin-right:.6rem}.amount-selector.is-small .amount-selector__input{width:2.4rem;height:2.4rem;line-height:2.4rem;font-size:1.2rem;margin-right:.2rem}.amount-selector.is-small .amount-selector__btn{width:2.4rem;height:2.4rem;font-size:1rem;margin-right:.2rem}